<!-- 功能授权 -->
<template>
    <div class="role">
       <base-title border title="功能授权">
       </base-title>
       <div class="content">
    <div class="search">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
            <el-form-item label="组织结构" prop="institutions">
                <div @click="structureTop">
                    <el-input
                            v-model="formInline.queryFilterText"
                            @blur="instBlur"
                            placeholder="请选择或输入"
                            @click.stop>
                        <i slot="suffix"  class="iconfont icon-apartment"></i>
                    </el-input>
                </div>
                <div class="el-div-tree" v-if="queryStruct">
                    <el-tree
                            class="filter-tree"
                            :data="labelData"
                            :props="defaultProps"
                            default-expand-all
                            :filter-node-method="filterNode"
                            @node-click="queryAddIns"
                            ref="tree">
                    </el-tree>
                </div>
            </el-form-item>
            <!--<el-input style="width:200px" placeholder="请选择组织结构"></el-input>-->
            <el-button class="sear-btn" @click="query(1)">查询</el-button>
            <el-button class="clear-btn" type="text" @click="query(2)">清空</el-button>
        </el-form>
    </div>
    <el-table  :data="tableData" style="width: 100%">
     <el-table-column prop="orderIndex" label="显示顺序"></el-table-column>
     <el-table-column label="角色名称" >
         <template slot-scope="scope">
            <router-link :to="{path:'/authorization/Layout/action-auth-detail',query:{id: scope.row.id}}">
                <span style="color: #4A80F6">
                    {{ scope.row.displayName }}
                </span>
            </router-link>
        </template>
     </el-table-column>
     <el-table-column prop="orgName" label="机构"></el-table-column>
     <el-table-column prop="notes" label="描述"></el-table-column>
     <el-table-column prop="sysCount" label="授权系统" >
         <template slot-scope="scope">
        <router-link to="/authorization/Layout/action-auth-detail" >
            <span style="color: #4A80F6">
                 {{ scope.row.number }}
            </span>
        </router-link>
        </template>
     </el-table-column>
    <el-table-column label="操作" width="60"></el-table-column>
        <template >
            <div>
                编辑
            </div>
            <div>
                删除
            </div>
        </template>

    </el-table>
       </div>
       <div class="footer">
    <page class="page" :pageTotal="pageTotal"></page>
       </div>
    </div>
</template>

<script>
export default {
    components:{
        baseTitle:()=>import("@/components/title"),
        page:()=>import("@/components/paging"),
      /*  orgSelect:()=>import("@/components/org-select"),*/

    },
    data(){
        return {
            //查询
            labelData:[],
            formInline:{
                queryFilterText:''
            },
            queryFilterText:'',
            queryStruct:false,
            defaultProps: {
                children: 'children',
                label: 'displayName'
            },
            //表格
            pageIndex:1,
            pageSize:10,
            tableData:[],
            pageTotal:0,
        }
    },
    created(){
        this.institutions();
        this.autList();
    },
    methods:{
        filterNode(value, data) {
            if (!value) return true;
            return data.displayName.indexOf(value) !== -1;
        },
        //点击机构
        queryAddIns(val){
            this.queryFilterText = val.id;
            this.formInline.queryFilterText = val.displayName;
            this.queryStruct = false;
        },
        structureTop() {
            this.queryStruct = !this.queryStruct
        },
        instBlur(){
            this.queryStruct = false;
        },
        //查询
        query(val){
            if(val == 1){
                this.autList();
            }else {
                this.queryFilterText = '';
                this.formInline.queryFilterText = '';
            }
        },
        //机构数据请求
        institutions(){
            let parameter = {
                level:0,
                onlyOrg:1,
                parentId:0
            };
            this.$api.institutions(parameter).then(res => {
                if(res.success == true){
                    this.labelData = res.data;
                }
            })
        },
        //分页查询
        autList(){
            let parameter = {
                orgId: this.queryFilterText,
                pageIndex:this.pageIndex,
                pageSize:this.pageSize
            };
            this.$api.autList(parameter).then(res => {
                if(res.success == true){
                    this.tableData = res.data;
                    this.pageTotal = parseInt(res.totalDatas);
                }
            })
        }
    }
}
</script>

<style lang="scss" scoped="scoped">
.role {
 padding: 18px 24px;
}
.content {
	.search {
    margin: 20px 0;
    display: flex;
    align-items: center;
    .title {
    padding-right: 5px;
    }
    .sear-btn {
    margin-left: 20px;
    padding: 10px 20px;
    }
    .clear-btn {
    text-decoration: underline;
    }
	}
}
.footer {
	text-align: right;
	margin-top: 25px;
	.page {
    display: inline-block
	}
}
.el-div-tree{
    position: absolute;
    top:45px;
    width: 100%;
    background-color: #fff;
    border: 1px solid #ebeef5;
    border-radius: 4px;
    -webkit-box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    z-index: 9999999!important;
    overflow: auto;
}
</style>
